img {
	width: 100%; }

.maske {
  position: fixed;
  z-index: 2000;
  overflow: hidden;
  width: 100vw;
  height: 100vh;
  background: #000;
  mix-blend-mode: darken;
  animation-name: slide;
  animation-duration: 3s;
  animation-fill-mode: forwards;
  @keyframes slide {
    99% {
      height: 100vh; }
    100% {
      height: 0vh; } }
  &__inner {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 150vw;
    height: 150vh;
    transform: translate(-50%, -50%) scale(0.01);
    border-radius: 50%;
    background: #fff;
    animation-name: slidein;
    animation-duration: 3s;
    animation-fill-mode: forwards;
    @keyframes slidein {
      0% {
        transform: translate(-50%, -50%) scale(0.01); }
      100% {
        transform: translate(-50%, -50%) scale(1); } } } }

.wrapper {
	max-width: 1400px;
	overflow: hidden;
	margin: 0 auto;
	position: relative;
	padding-bottom: 41px;
	background: url('../img/bg.jpg') repeat; }

.container {
	max-width: 940px;
	margin: 0 auto;
	@include clr;
	@include r(960) {
		margin: 0 30px; }
 }	// overflow: hidden

.header {
	position: fixed;
	left: 0;
	right: 0;
	top: 0;
	z-index: 100;
	.header__left {
		float: left;
		@include r(485) {
			float: none;
			text-align: center; } }
	.header__right {
		float: right;
		@include r(485) {
			float: none; } } }

.logo {
	@include font;
	// font-family: 'bebas_neue_regularregular'
	font-size: 36px;
	color: #e8603c;
	display: block;
	text-transform: uppercase;
	text-decoration: none;
	padding: 15px 0px;
	&__color {
		color: #f3af9d; } }

.nav {
	list-style: none;
	text-align: right;
	@include r(420) {
		text-align: center; }
	&__item {
		display: inline-block;
		@include r(420) {
			text-align: center; } }
	&__link {
		@include font;
		text-decoration: none;
		display: block;
		color: #b9b9b9;
		// font-family: 'bebas_neue_regularregular'
		font-size: 16px;
		font-weight: 400;
		padding: 25px 0 27px 28px;
		@include r(420) {
			padding: 0 10px; } }
	.is-active {
		color: #e8603c; } }

.blog {
	overflow: hidden;
	position: relative;
	margin: 70px 0 24px;
	@include r(490) {
		margin: 120px 0 24px; }
	&__left {
		position: absolute;
		left: 0; }
	&__right {
		position: absolute;
		right: 0;
		top: 0; } }

.block {
	height: 61px;
	width: 220px;
	@include r(1400) {
		display: none; }
	&_pink {
		background-color: rgba(232, 96, 60, 0.5); } }

.blog-content {
	background-color: #e8603c;
	&__title {
		@include font;
		color: white;
		// font-family: 'bebas_neue_regularregular'
		font-size: 24px;
		font-weight: 400;
		padding: 17px 0 18px 17px; } }

.post {
	margin-bottom: 57px; }

.section {
	&__left {
		float: left;
		max-width: 670px;
		position: relative;
		@include r(965) {
			float: none;
			margin: 0 auto; } }
	&__right {
		float: right;
		@include r(965) {
			float: none;
			text-align: center;
			margin-top: 40px; } }
	&__photo {
		margin-bottom: 23px; } }

.icon {
	position: absolute;
	left: -85px;
	top: 0;
	width: 75px;
	height: 75px;
	&:hover {
		border-radius: 50%; }
	@include r(1130) {
		position: static;
		margin-bottom: 20px;
		@include r(650) {
			width: 50px;
			height: 50px; } }
	&_post-first {
		background: url('../img/icon-first.png') #e8603c  no-repeat center;
		transition: 2s;
		&:hover {
			transform: rotate(360deg); } }
	&_post-second {
		background: url('../img/icon2.png') #e8603c  no-repeat center;
		transition: 2s;
		&:hover {
			transform: rotate(360deg); } }
	&_quotes-white {
		background: url('../img/quotes-white.png')  #e8603c  no-repeat center;
		transition: 2s;
		&:hover {
			transform: rotate(360deg); } }
	&_link {
		background: url('../img/link.png')  #e8603c  no-repeat center;
		transition: 2s;
		&:hover {
			transform: rotate(360deg); } } }

.content {
	overflow: hidden;
	padding-right: 10px;
	&__left {
		float: left;
		position: relative;
		@include r(705) {
			float: left; } }
	&__right {
		float: left;
		max-width: 500px;
		margin-left: 51px; } }

.info {
	margin-bottom: 16px;
	font-size: 12px;
	font-weight: 400;
	@include r(705) {
		display: inline-block;
		text-align: center;
		margin: 0 20px 20px 0; }
	&__description {
		color: #bababa;
		display: block;
		padding-top: 10px;
		text-decoration: none;
		// span
 } }		// 	font-weight: 300

.h3 {
