		padding-right: 20px;
		float: left;
		background: url('../img/bg.jpg') repeat; } }

.link {
	.link__item {
		color: #464646;
		display: block;
		text-decoration: none; } }

.gallery {
	font-size: 0;
	margin: 19px -3px 0;
	&__block {
		display: inline-block;
		width: 50px;
		height: 50px;
		border: 1px solid #e8603c;
