		color: #a0a0a0;
		margin: 10px 0 25px;
		font-size: 12px;
		font-weight: 400; } }

.page-number {
	@include r(965) {
		text-align: center; }
	&__item {
		@include font;
		display: inline-block;
		width: 26px;
		height: 26px;
		text-align: center;
		line-height: 26px;
		text-decoration: none;
		color: white;
